<template>
  <div class="order-store">
    <van-checkbox @change="change" style="padding-right: 0.36rem;" v-if="showCheckbox" v-model="store.checked" icon-size="0.36rem" checked-color="#E1244E" />
    <img @click.stop="goStore" :src="store.avatar || $store.state.defaultAvatar" alt="">
    <p @click.stop="goStore">{{ store.nick_name }} <van-icon v-if="click" name="arrow" color="#AEAEAE" /></p>
    <div style="flex: 1"></div>
    <span v-if="statusText">{{ statusText }}</span>
  </div>
</template>

<script>
export default {
  name: 'order-store',
  props: {
    store: {
      type: Object,
      default() {
        return {}
      }
    },
    showCheckbox: {
      type: Boolean,
      default: false
    },
    statusText: {
      type: String,
      default: ''
    },
    index: {
      type: Number,
      default: 0
    },
    click: {
      type: Boolean,
      default: true
    }
  },
  beforeDestroy() {
    this.$off('toggleStore')
  },
  methods: {
    goStore() {
      if (!this.click) return
      this.$router.push('/stores/' + this.store.id)
    },
    change() {
      this.$emit('toggleStore', { index: this.index })
    }
  }
}
</script>

